import React from 'react';
import '../index.css';
import styles from './Square.module.css'

function Square(props) { 
  return (
    <button className={styles.square} onClick = {props.onClick}> 
      {props.value} 
    </button>
    // <button className="square" onClick = {props.onClick}> 
    //   {props.value} 
    // </button>
  );
}

export default Square;