import {React, useState} from 'react';
import '../index.css';
import { useSelector, useDispatch } from 'react-redux';
import { setHistory, setStepNumber, setXIsNext} from '../features/game/gameSlice';
import greeting from '../hello.png';
import pony from '../sleeppony.gif';
import Board from './Board';
import styles from './Game.module.css'
import Modal from 'react-modal';
import ponywave from '../ponywave.gif'

function Game(props) {
  
  // select states from redux store
  const history = useSelector((state) => state.game.history);
  const stepNumber = useSelector((state) => state.game.stepNumber);
  const xIsNext = useSelector((state) => state.game.xIsNext);

  const dispatch = useDispatch();
  
  const handleClick = i => {
    const hist = history.slice(0, stepNumber + 1); //copy of history up to current step
    const current = hist[hist.length -1]; // current view of grid (current element in the history )
    const squares = current.squares.slice(); // create copy to avoid mutating underlying data directly
    if (calculateWinner(squares) || squares[i]) { // dont allow click when there's a winner or square is not null (i.e. it's filled in)
      return;
    }
    squares[i] = xIsNext ? 'ðŸ¦†': 'ðŸ‘';
    // dispatch actions/reducer from redux to update states globally
    dispatch(setHistory(hist.concat([{ squares: squares}])));
    dispatch(setStepNumber(hist.length));
    dispatch(setXIsNext(!xIsNext));
    
  }

  const jumpTo = step => {
    dispatch(setStepNumber(step));
    dispatch(setXIsNext((step % 2) === 0));
  }

  // used to be in render 
  // removed render() so now must define history differently
  const hist = history;
  const current = hist[stepNumber];
  const winner = calculateWinner(current.squares);

  //const [winner, setWinner] = useState(calculateWinner(current.squares));

    // iterate through history array and map (element, index)
    const moves = hist.map((step, move) => { 
      const desc = move ? // if square has been clicked (i.e. grid added to history, element >=1)
      'Go to move #' + move :
      'Go to game start'; 
      return ( // render a button
      // assign key to list of buttons? where key in this case is index (moves are never reordered, deleted or inserted in middle)
        <li className={styles.history} key={move}> 
          <button className={styles.history} onClick={() => jumpTo(move)}>{desc}</button>
        </li>
      );
    });

    let status;
    if (winner) {
      status = 'Winner: ' + winner;
    } else if (!winner && current.squares.every(element => element !== null)) {
      status = "It's a tie! " ;
    } else {
      status = 'Next player: ' + (xIsNext ? 'ðŸ¦†' : 'ðŸ‘');
    }

    const [modalIsOpen,setModalIsOpen] = useState(false);

    const setModalIsOpenToTrue =()=>{
        setModalIsOpen(true)
    }

    const setModalIsOpenToFalse =()=>{
        setModalIsOpen(false);
    }

    return (
      <div>
        <h1>My Little Tic Tac Toe-ny</h1>

        <button className={styles.open} onClick={setModalIsOpenToTrue}>Click here to say Hi!!</button>

        <Modal className={styles.modal} isOpen={modalIsOpen} onRequestClose={()=> setModalIsOpen(false)}>
          <button className={styles.close} onClick={setModalIsOpenToFalse}>Close</button>
          <div><img className={styles.ponywave} src={ponywave} alt="ponywave" width = "900px"/></div>
        </Modal>
      

        <div className={styles.game}>
          <div className="board">
            <Board 
              //squares ={current.squares} // displays current grid (up to stepNumber which is defined in jumpTo which is based on move (index of history, i.e. what turn the game is on))
              squares = {useSelector((state) => state.game.history[state.game.stepNumber].squares)}
              onClick={(i) => handleClick(i)}
            />
          </div>
          <div>
            <div><img className={styles.greeting} src={greeting} alt = "greeting" width = "400px" /></div>
            <div><img className={styles.pony} src={pony} alt="pony" width = "900px"/></div>
          </div>
          <div className={styles.info}>
            {/* <div><img className={styles.greeting} src={greeting} alt = "greeting" width = "300px" /></div> */}
            <div className={styles.status}>{status}</div>
            <ol>{moves}</ol>
          </div>
        </div>
      </div>
    );
  
}

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
}

export default Game;
