import React from 'react';
import '../index.css';
import { useSelector } from 'react-redux';
import Square from './Square';
import styles from './Board.module.css'

function Board(props) {

    const squares = useSelector((state) => state.game.history[state.game.stepNumber].squares);
  
    const renderSquare = i => {
      return (
        <Square 
          value = {squares[i]}
          onClick = {() => props.onClick(i)} 
        />
      );
    }
  
      return (
        <div className={styles.grid}>
            {renderSquare(0)}
            {renderSquare(1)}
            {renderSquare(2)}
            {renderSquare(3)}
            {renderSquare(4)}
            {renderSquare(5)}
            {renderSquare(6)}
            {renderSquare(7)}
            {renderSquare(8)}
        </div>
      );
    }

export default Board;